body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app{font-size:18px;font-family:"Globotipo Variable","Globotipo","Globotipo UI","Globotipo Display","Globotipo Book",Avenir,Ubuntu,-apple-system,system-ui,BlinkMacSystemFont,sans-serif}

.main{padding:30px 400px 0 300px}@media (max-width: 619px){.main{padding:20px}}

h1,h2{text-align:center}h3{font-weight:normal}

.container{display:flex;align-items:center;justify-content:center;margin:60px 0}@media (max-width: 619px){.container{margin:10px 0}}.form-group{margin-bottom:15px}.error{color:#c4170c;font-size:12px}.form-input,.submit{width:466px;height:48px;padding-left:10px;border-radius:10px;border:none;outline:none;background-color:#ECEDEF}@media (max-width: 619px){.form-input,.submit{width:300px;height:30px}}.form-input::placeholder,.submit::placeholder{padding-left:15px}.submit{width:478px;background-color:#c4170c;color:#fff}@media (max-width: 619px){.submit{width:312px}}.submit:hover{background-color:#ECEDEF;color:black;border:1px solid #6e727a}.form-phone{width:478px}@media (max-width: 619px){.form-phone{width:312px}}

.recommended{display:flex;grid-gap:10px;gap:10px;width:650px;margin:60px 0 30px 0}@media (max-width: 619px){.recommended{width:auto}}.image{object-fit:contain;width:190px;height:108px;align-self:center}.top-right{display:flex;align-items:center;margin-bottom:10px}.star{height:24px}.star-text{margin:0}.r-text{text-decoration:none;color:#c4170c;font-weight:600}.r-text:hover{color:#A7140C}

.t-top{display:flex;align-items:center;grid-gap:7px;gap:7px}.trend-ico{height:28px}.t-title{font-size:24px;font-family:"glbOpenSans","Open Sans","opensans","Globotipo Variable",sans-serif;font-weight:normal}.bold{font-weight:600}.trend{display:flex;grid-gap:15px;gap:15px}@media (max-width: 619px){.trend{grid-gap:0;gap:0}}.position{color:#6e727a;font-family:"glbOpenSans","Open Sans","opensans","Globotipo Variable",sans-serif;line-height:48px;font-size:40px}.t-link{text-decoration:none;color:#c4170c;font-weight:600}.t-link:hover{color:#A7140C}.t-img{height:120px;border-radius:8px;align-self:center}@media (max-width: 619px){.t-img{width:100px;height:76px;object-fit:cover}}.t-line{height:1px;background-color:#ECEDEF;border-radius:4px;margin:20px 0}

.title{font-size:50px;margin-top:100px}@media (max-width: 619px){.title{margin-top:50px;font-size:30px}}

.header-top{display:flex;padding:0 50px;justify-content:space-between;align-items:center}@media (max-width: 619px){.header-top{padding:0}}.left{display:flex;grid-gap:10px;gap:10px}@media (max-width: 619px){.left>:nth-child(n+6){display:none}}.link{padding:12px 11px;font-family:"Globotipo Variable","Globotipo","Globotipo UI","Globotipo Display","Globotipo Book",Avenir,Ubuntu,-apple-system,system-ui,BlinkMacSystemFont,sans-serif;font-size:16px;font-weight:600;text-decoration:none}.right-link{color:black}.right-link:hover{color:#1063E0}.h-right{margin:0 30px}@media (max-width: 619px){.h-right{display:none}}.link:hover .person-svg path{fill:#1063E0}.left-link{position:relative}.left-link::after{content:'';position:absolute;bottom:0;left:0;width:100%;height:2px;background-color:transparent;transition:background-color 0.3s ease}.left-link:hover::after{background-color:var(--hover-line)}

.header{height:74px;padding:0 50px;display:flex;align-items:center;justify-content:space-between;background-color:#c4170c}@media (max-width: 619px){.header{height:50px;padding:0 9px}}.left{display:flex;grid-gap:10px;gap:10px}.burger-menu{display:flex;flex-direction:column;grid-gap:4px;gap:4px;padding-top:7px}.menu{padding-top:6px;color:#fff;font-weight:bold;text-transform:uppercase;font-size:14px}@media (max-width: 619px){.menu{display:none}}.line{height:2px;width:20px;background-color:#fff;border-radius:2px}@media (max-width: 619px){.squares{display:none}}.square{width:1px;height:1px;margin:3px;background-color:#fff}.logo{text-decoration:none;font-family:"glbOpenSans","Open Sans","opensans","Globotipo Variable",sans-serif;color:#fff;font-size:32px;font-weight:300;text-transform:uppercase;transition:opacity 300ms ease;text-overflow:eclipsis}.logo:hover{opacity:.7}@media (max-width: 619px){.logo{font-size:24px}}@media (max-width: 619px){.g1{height:28px;width:28px}}@media (max-width: 619px){.search{display:none}}@media (min-width: 1024px){.loupe-on-phone{display:none}}.search{position:relative}.search-input{width:164px;height:33px;padding:0 40px;background-color:#A7140C;border:none;border-radius:5px;transition:width 0.3s ease}.search-input::placeholder{color:#fff;font-weight:600}.search-input:focus{background-color:#fff;outline:none;width:220px}.search-input:focus::placeholder{color:#6e727a}.search-input:focus+.loupe{fill:#6e727a}.loupe{position:absolute;top:5px;left:10px}.close{background:none;border:none;position:absolute;right:5px;top:8px}

.done{padding-top:20px;display:flex;flex-direction:column;align-items:center}


